/**
 * Functions that are dispatched from action creators
 */

/**
 *
 * @param {string} type
 * @param {boolean} payload
 * @returns {object}
 */
export const loading = (type, payload) => {
  return {
    type,
    payload
  };
};

/**
 *
 * @param {string} type
 * @param {*} payload
 * @returns {object}
 */
export const error = (type, payload) => {
  return {
    type,
    payload
  };
};

/**
 *
 * @param {string} type
 * @param {*} payload
 * @returns {object}
 */
export const dataPayload = (type, payload = null) => {
  return { type, payload };
};
