import React from 'react';
import { Table, Icon } from 'semantic-ui-react';
import moment from 'moment'

function InstanceItem({ item }) {
  return (
    <Table.Row>
      <Table.Cell><Icon name='file text' />{item.name}</Table.Cell>
      <Table.Cell>{item.description}</Table.Cell>
      <Table.Cell>{moment(item.createdAt).format(
        'DD/MM/YYYY'
      )}</Table.Cell>
      <Table.Cell>
        <a href={item.url} target="_blank" rel="noopener noreferrer"><Icon name='download' color={'black'} /></a>
      </Table.Cell>
    </Table.Row>
  );
}

export default InstanceItem;
