import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { ToastContainer, Zoom } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import authenticatedComponent from 'components/middlewares/AuthenticatedComponent';
import protectedComponent from 'components/middlewares/ProtectedComponent';
import Homepage from 'components/pages/homepage/Homepage';
import Instances from 'components/pages/instances/Instances';
import Solutions from 'components/pages/solutions/Solutions';
import AllSolutions from 'components/pages/allSolutions/AllSolutions';
import Validation from 'components/pages/validation/Validation';
import Login from 'components/pages/auth/login/Login';
import Register from 'components/pages/auth/register/Register';
import Profile from 'components/pages/profile/Profile';
import NotFound from 'components/pages/notFound/NotFound';

class App extends Component {
  render() {
    return (
      <Router basename={process.env.PUBLIC_URL}>
        <ToastContainer
          position='top-right'
          autoClose={3500}
          hideProgressBar={true}
          newestOnTop
          closeOnClick
          rtl={false}
          pauseOnVisibilityChange
          draggable={false}
          pauseOnHover
          transition={Zoom}
        />
        <Switch>
          <Route exact path={'/'} component={Homepage} />
          <Route path={'/login'} component={authenticatedComponent(Login)} />
          <Route
            path={'/register'}
            component={authenticatedComponent(Register)}
          />
          <Route path='/instances' component={protectedComponent(Instances)} />
          <Route
            path='/my-solutions'
            component={protectedComponent(Solutions)}
          />
          <Route
            path='/leaderboard'
            component={protectedComponent(AllSolutions)}
          />
          <Route
            path='/validation'
            component={protectedComponent(Validation)}
          />
          <Route path='/profile' component={protectedComponent(Profile)} />
          <Route component={NotFound} />
        </Switch>
      </Router>
    );
  }
}

export default App;
