import React, { Component } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import { Formik } from 'formik';
import * as Yup from 'yup'; // for everything
import { connect } from 'react-redux';
import { Container, Segment, Header, Divider, Icon } from 'semantic-ui-react';
import { InputElement, ButtonPrimary } from 'components/shared';
import { loginUser } from 'actions';
import '../auth.scss';

const emailRegEx = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;

const validation = Yup.object().shape({
  email: Yup.string()
    .matches(emailRegEx, {
      message: 'Email is not valid',
      excludeEmptyString: true
    })
    .required('Email is required'),
  password: Yup.string().required('Password is required')
});

class Login extends Component {
  constructor(props) {
    super(props);

    this.state = {
      redirect: false,
      initialFormValues: {
        email: '',
        password: ''
      }
    };
  }
  render(
    { loginUser, login_loading, login_error, login_success } = this.props,
    { redirect } = this.state
  ) {
    if (redirect) {
      return <Redirect to='/' />;
    }
    return (
      <Container fluid className='auth-container'>
        <Helmet>
          <title>Login | Photo Slideshow FIEK</title>
        </Helmet>
        <Segment className='auth-container-seg'>
          <Header as='h2' className='center aligned'>
            Photo slideshow FIEK
            <Header.Subheader>
              Log in to download instances and test the solutions
            </Header.Subheader>
          </Header>
          <Formik
            initialValues={this.state.initialFormValues}
            validationSchema={validation}
            onSubmit={(values, { setSubmitting }) => {
              const { email, password } = values;
              loginUser(email, password).then(() =>
                this.setState({ redirect: true })
              );
              // setSubmitting(false);
            }}
          >
            {({
              values,
              errors,
              touched,
              handleChange,
              handleBlur,
              handleSubmit,
              isSubmitting
            }) => (
                <form onSubmit={handleSubmit} className='form-group-container'>
                  <InputElement
                    label='Enter your email address'
                    iconName='at'
                    error={errors.email ? true : false}
                    name='email'
                    value={values.email}
                    onChange={handleChange}
                  />
                  <InputElement
                    label='Your password here'
                    iconName='lock'
                    type='password'
                    error={errors.password ? true : false}
                    name='password'
                    value={values.password}
                    onChange={handleChange}
                  />
                  <ButtonPrimary
                    title={'Log In'}
                    loading={login_loading}
                    disabled={
                      values.email === '' ||
                        errors.email ||
                        values.password === '' ||
                        errors.password
                        ? true
                        : false
                    }
                  />
                </form>
              )}
          </Formik>
          <Divider horizontal>or</Divider>
          <Segment className='auth-footer'>
            <p className='center aligned'>Don't have an account?</p>
            <Link to='/register'>Sign up</Link>
          </Segment>
          {login_error && (
            <p className='auth-error'>
              <Icon name='warning' /> {login_error}
            </p>
          )}
        </Segment>
      </Container>
    );
  }
}

const mapStateToProps = (state) => {
  const { login_loading, login_error, login_success } = state.auth;

  return { login_loading, login_error, login_success };
};

export default connect(mapStateToProps, {
  loginUser
})(Login);
