import React, { Component } from 'react';
import { Header } from 'semantic-ui-react';
import { PageContainer } from 'components/shared';

class Homepage extends Component {
  render() {
    return (
      <PageContainer className='homepage-container' title='Homepage'>
        <Header as='h2'>
          Photo Slideshow FIEK
          <Header.Subheader>
            Given a list of photos and the tags associated with each photo,
            arrange the photos into a slideshow that is as interesting as
            possible (the scoring section below explains what we mean by
            “interesting”).
          </Header.Subheader>
        </Header>
        Read more about the problem on this{' '}
        <a
          target='_blank'
          rel='noopener noreferrer'
          href='https://storage.googleapis.com/coding-competitions.appspot.com/HC/2019/hashcode2019_qualification_task.pdf'
        >
          link
        </a>
      </PageContainer>
    );
  }
}

export default Homepage;
