import React from 'react';
import { Divider, Icon } from 'semantic-ui-react';
import './footer.scss';

function Footer() {
  let date = new Date();
  return (
    <footer>
      <Divider />
      <Icon name='copyright outline' />
      {date.getFullYear()}
      <p>Photo Slideshow FIEK</p>
    </footer>
  );
}

export default Footer;
