import React from 'react';
import { Menu, Icon } from 'semantic-ui-react';
import { NavLink } from 'react-router-dom';

const logInButton = () => {
  if (localStorage.getItem(process.env.REACT_APP_ACCESS_TOKEN)) {
    return (
      <>
        <Menu.Item name='profile' as={NavLink} to='/profile' color={'teal'}>
          <Icon name='user circle outline' />
          Profile
        </Menu.Item>
        <Menu.Item
          name='login'
          onClick={() => (
            localStorage.removeItem(process.env.REACT_APP_ACCESS_TOKEN),
            window.location.replace('/')
          )}
        >
          <Icon name='log out' />
          Logout
        </Menu.Item>
      </>
    );
  }
  return <Menu.Item name='login' as={NavLink} to='/login' />;
};

function Header() {
  return (
    <Menu pointing secondary>
      <Menu.Item name='home' as={NavLink} exact to='/' color={'teal'}>
        <Icon name='home' /> Home
      </Menu.Item>
      <Menu.Item name='instances' as={NavLink} to='/instances' color={'teal'} />
      <Menu.Item
        name='validation'
        as={NavLink}
        to='/validation'
        color={'teal'}
      />
      <Menu.Item
        name='My solutions'
        as={NavLink}
        to='/my-solutions'
        color={'teal'}
      />
      <Menu.Item
        name='Leaderboard'
        as={NavLink}
        to='/leaderboard'
        color={'teal'}
      />
      <Menu.Menu position='right'>{logInButton()}</Menu.Menu>
    </Menu>
  );
}

export default Header;
