import { getRequest, postRequest, access_token } from './apiMethods';

const getSolutionsApi = () => {
  let url = 'solutions';
  let headers = {
    'x-auth-token': access_token()
  };

  return getRequest(url, headers);
};

const getAllSolutionsApi = () => {
  let url = 'solutions/leaderboard';
  let headers = {
    'x-auth-token': access_token()
  };

  return getRequest(url, headers);
};

const validateSolutionApi = (file, fileDetails, instance) => {
  let url = 'solutions/validate'
  let headers = {
    'x-auth-token': access_token()
  }
  let data = {
    file,
    fileDetails,
    instance
  }

  return postRequest(url, headers, data);
}

export { getSolutionsApi, getAllSolutionsApi, validateSolutionApi };
