import React from 'react';
import { Input } from 'semantic-ui-react';

const InputElement = ({
  name,
  label,
  iconName,
  type = 'text',
  error = false,
  onChange,
  value
}) => (
  <Input
    name={name}
    icon={iconName}
    iconPosition='left'
    placeholder={label}
    type={type}
    error={error}
    onChange={onChange}
    value={value}
  />
);

export default InputElement;
