import axiosInstance from './apiInstances';

// GET Request
/**
 *
 * @param {*} url
 * @param {*} headers
 * @param {*} params
 */
const getRequest = (url, headers = null, params = null) => {
  return axiosInstance.get(url, {
    headers,
    params
  });
};

// POST Request
/**
 *
 * @param {*} url
 * @param {*} headers
 * @param {*} data
 * @param {*} params
 */
const postRequest = (url, headers = null, data = null, params = null) => {
  return axiosInstance.post(url, data, {
    params,
    headers
  });
};

// PUT Request
/**
 *
 * @param {*} url
 * @param {*} headers
 * @param {*} data
 * @param {*} params
 */
const putRequest = (url, headers = null, data = null, params = null) => {
  return axiosInstance.put(url, data, { params, headers });
};

// DELETE Request
/**
 *
 * @param {*} url
 * @param {*} headers
 * @param {*} params
 */
const deleteRequest = (url, headers, params) => {
  return axiosInstance.delete(url, {
    headers,
    params
  });
};

// Token of logged user
const access_token = () => {
  let access_token_value = localStorage.getItem(process.env.REACT_APP_ACCESS_TOKEN);
  return `Bearer ${access_token_value}`;
};

export { getRequest, postRequest, putRequest, deleteRequest, access_token };
