import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import moment from 'moment'
import { PageContainer } from 'components/shared';
import { Header, Loader, Message, Card, Image } from 'semantic-ui-react';
import { getUserDetails } from 'actions'
import { UserPng } from 'assets/images'
import "./profile.scss"

const renderUserDetails = (userDetails, userDetailsLoading, userDetailsError) => {
  if (userDetailsLoading) {
    return <Loader active inline='centered' />
  }
  if (userDetailsError) {
    return <Message
      warning
      content={userDetailsError}
    />
  }
  if (userDetails === null) {
    return <Message
      content='No user details were found'
    />
  }
  return (
    <div className='profile-details'>
      <Card>
        <Image src={UserPng} wrapped ui={false} />
        <Card.Content>
          <Card.Header>{userDetails.name + ' ' + userDetails.lastname}</Card.Header>
          <Card.Meta>
            <span className='date'>{`Joined on ${moment(userDetails.created_at).format(
              'DD/MM/YYYY HH:mm'
            )}`}</span><br />
            <span className='date'>{`Last updated on ${moment(userDetails.updatedAt).format(
              'DD/MM/YYYY HH:mm'
            )}`}</span>
          </Card.Meta>
          <Card.Description>
            <a href={`mailto:${userDetails.email}`}>{userDetails.email}</a>
          </Card.Description>
        </Card.Content>
      </Card>
    </div>
  )
}

const Profile = () => {
  const { userDetails, userDetailsLoading, userDetailsError } = useSelector(state => state.auth)
  const dispatch = useDispatch()
  useEffect(() => {
    !userDetails && dispatch(getUserDetails())
  }, [])
  return (
    <PageContainer className='profile-container' title='Profile'>
      <Header as='h2'>Profile Details</Header>
      {renderUserDetails(userDetails, userDetailsLoading, userDetailsError)}
    </PageContainer>
  );
}

export default Profile;
