import React from 'react';
import { PageContainer } from 'components/shared';
import { Header } from 'semantic-ui-react';

function NotFound() {
  return (
    <PageContainer className='notFound-container' title='Not Found'>
      <Header as='h2'>
        404 Not Found
        <Header.Subheader>
          The result you were looking not found
        </Header.Subheader>
      </Header>
    </PageContainer>
  );
}

export default NotFound;
