import { postRequest, getRequest, access_token } from './apiMethods';

const loginUserApi = (email, password) => {
  let url = 'auth';
  let data = {
    email,
    password
  };
  return postRequest(url, null, data);
};

const registerUserApi = (name, lastname, email, password, confirmPassword) => {
  let url = 'users/register';
  let data = {
    name,
    lastname,
    email,
    password,
    confirmPassword
  };
  return postRequest(url, null, data);
};

const userDetailsApi = () => {
  let url = 'users/me'
  let headers = {
    'x-auth-token': access_token()
  };

  return getRequest(url, headers);
}

export { loginUserApi, registerUserApi, userDetailsApi };
