import React from 'react';
import { Table, Icon } from 'semantic-ui-react';

const showResultForInstance = (instanceItem) => {
  if (instanceItem) {
    return (
      <>
        {instanceItem.score}{" "}
        <a href={instanceItem.url} target='_blank' rel='noopener noreferrer'>
          <Icon name='download' color={'black'} />
        </a>
      </>
    );
  }
  return '-';
};

function SolutionItem({ item, index }) {
  return (
    <Table.Row>
      <Table.Cell>{index + 1}</Table.Cell>
      <Table.Cell>
        <Icon name='user' />
        {item.userId.name + ' ' + item.userId.lastname}
      </Table.Cell>
      <Table.Cell textAlign='center' verticalAlign='middle'>
        {showResultForInstance(item.a_example)}
      </Table.Cell>
      <Table.Cell textAlign='center' verticalAlign='middle'>
        {showResultForInstance(item.b_lovely_landscapes)}
      </Table.Cell>
      <Table.Cell textAlign='center' verticalAlign='middle'>
        {showResultForInstance(item.c_memorable_moments)}
      </Table.Cell>
      <Table.Cell textAlign='center' verticalAlign='middle'>
        {showResultForInstance(item.d_pet_pictures)}
      </Table.Cell>
      <Table.Cell textAlign='center' verticalAlign='middle'>
        {showResultForInstance(item.e_shiny_selfies)}
      </Table.Cell>
      <Table.Cell textAlign='center' verticalAlign='middle'>
        {item.totalScore}
      </Table.Cell>
    </Table.Row>
  );
}

export default SolutionItem;
