import Types from 'constants/types';
import api from 'constants/api';
import { errorsHandler, loading, dataPayload, error } from 'utilities';

/**
 * Component - Instances
 * Get Instances
 *
 * @returns {object} - status, data
 */
export const getInstances = () => async (dispatch) => {
  dispatch(loading(Types.GET_INSTANCES_LOADING, true));
  dispatch(error(Types.GET_INSTANCES_ERROR, ''));
  try {
    let result = await api.getInstancesApi();
    const { status, data } = result;
    if (status === 200) {
      dispatch(dataPayload(Types.GET_INSTANCES_SUCCESS, data.data))
    } else {
      dispatch(error(Types.GET_INSTANCES_ERROR, 'Something went wrong!'));
    }
    dispatch(loading(Types.GET_INSTANCES_LOADING, false));
  } catch (ex) {
    dispatch(loading(Types.GET_INSTANCES_LOADING, false));
    dispatch(error(Types.GET_INSTANCES_ERROR, errorsHandler(ex)));
  }
};