import React, { Component } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import { Formik } from 'formik';
import * as Yup from 'yup'; // for everything
import { connect } from 'react-redux';
import { registerUser } from 'actions';
import { Container, Segment, Header, Divider, Icon } from 'semantic-ui-react';
import { InputElement, ButtonPrimary } from 'components/shared';
import '../auth.scss';

const alphaRegEx = /^[a-zA-Z_]+([a-zA-Z_]+)*$/;
const emailRegEx = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;
// const passwordRegEx = /([A-Z^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!`#@$%^&*-]).{6,}$/g;

const validation = Yup.object().shape({
  name: Yup.string()
    .matches(alphaRegEx, {
      message: 'Name is not valid',
      excludeEmptyString: true
    })
    .required('Name is required')
    .max(25, 'Name can not have more than 25 characters'),
  lastname: Yup.string()
    .matches(alphaRegEx, {
      message: 'Last name is not valid',
      excludeEmptyString: true
    })
    .required('Last name is required')
    .max(25, 'Last name can not have more than 25 characters'),
  email: Yup.string()
    .matches(emailRegEx, {
      message: 'Email is not valid',
      excludeEmptyString: true
    })
    .required('Email is required'),
  password: Yup.string()
    .min(6, 'Password should have at least 6 characters')
    .required('Password is required'),
  confirmPassword: Yup.string()
    .min(6, 'Password should have at least 6 characters')
    .oneOf([Yup.ref('password'), null], 'Passwords do not match')
    .required('Password confirmation is required')
});

class Register extends Component {
  constructor(props) {
    super(props);

    this.state = {
      redirect: false,
      initialFormValues: {
        name: '',
        lastname: '',
        email: '',
        password: '',
        confirmPassword: ''
      }
    };
  }
  render(
    { register_loading, register_error, registerUser } = this.props,
    { redirect } = this.state
  ) {
    if (redirect) {
      return <Redirect to='/' />;
    }
    return (
      <Container fluid className='auth-container'>
        <Helmet>
          <title>Register | Photo Slideshow FIEK</title>
        </Helmet>
        <Segment className='auth-container-seg'>
          <Header as='h2' className='center aligned'>
            Photo slideshow FIEK
            <Header.Subheader>
              Register to download instances and test the solutions
            </Header.Subheader>
          </Header>
          <Formik
            initialValues={this.state.initialFormValues}
            validationSchema={validation}
            onSubmit={(values, { setSubmitting }) => {
              const {
                name,
                lastname,
                email,
                password,
                confirmPassword
              } = values;
              registerUser(
                name,
                lastname,
                email,
                password,
                confirmPassword
              ).then((res) => this.setState({ redirect: true }));
              // setSubmitting(false);
            }}
          >
            {({
              values,
              errors,
              touched,
              isValid,
              handleChange,
              handleBlur,
              handleSubmit,
              isSubmitting
            }) => (
              <form onSubmit={handleSubmit} className='form-group-container'>
                <InputElement
                  label='Name'
                  iconName='user'
                  error={errors.name ? true : false}
                  name='name'
                  value={values.name}
                  onChange={handleChange}
                />
                <InputElement
                  label='Last name'
                  iconName='user'
                  error={errors.lastname ? true : false}
                  name='lastname'
                  value={values.lastname}
                  onChange={handleChange}
                />
                <InputElement
                  label='Enter your email address'
                  iconName='at'
                  error={errors.email ? true : false}
                  name='email'
                  value={values.email}
                  onChange={handleChange}
                />
                <InputElement
                  label='Your password here'
                  iconName='lock'
                  type='password'
                  error={errors.password ? true : false}
                  name='password'
                  value={values.password}
                  onChange={handleChange}
                />
                <InputElement
                  label='Confirm your password'
                  iconName='lock'
                  type='password'
                  error={errors.confirmPassword ? true : false}
                  name='confirmPassword'
                  value={values.confirmPassword}
                  onChange={handleChange}
                />
                <ButtonPrimary
                  title={'Sign up'}
                  loading={register_loading}
                  disabled={
                    values.name === '' ||
                    errors.name ||
                    values.lastname === '' ||
                    errors.lastname ||
                    values.email === '' ||
                    errors.email ||
                    values.password === '' ||
                    errors.password ||
                    values.confirmPassword === '' ||
                    errors.confirmPassword
                      ? true
                      : false
                  }
                />
              </form>
            )}
          </Formik>
          <Divider horizontal>or</Divider>
          <Segment className='auth-footer'>
            <p className='center aligned'>Have an account?</p>
            <Link to='/login'>Log in</Link>
          </Segment>
          {register_error && (
            <p className='auth-error'>
              <Icon name='warning' /> {register_error}
            </p>
          )}
        </Segment>
      </Container>
    );
  }
}

const mapStateToProps = (state) => {
  const { register_loading, register_error, register_success } = state.auth;

  return { register_loading, register_error, register_success };
};

export default connect(mapStateToProps, { registerUser })(Register);
