import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { PageContainer } from 'components/shared';
import { Header, Table, Message, Loader } from 'semantic-ui-react';
import { v4 as uuid } from 'uuid';
import { getSolutions } from 'actions'
import SolutionItem from './SolutionItem';
import './solutions.scss';

const renderSolutions = (solutions, solutions_error, solutions_loading) => {
  if (solutions_loading) {
    return <Loader active inline='centered' />
  }
  if (solutions_error) {
    return <Message
      warning
      header={solutions_error}
      content='Visit our registration page, then try again.'
    />
  }
  if (solutions.length === 0) {
    return <Message
      content='No solutions were found.'
    />
  }
  return (
    <Table color='teal'>
      <Table.Header>
        <Table.Row>
          <Table.HeaderCell>Name</Table.HeaderCell>
          <Table.HeaderCell>Post date</Table.HeaderCell>
          <Table.HeaderCell>Score</Table.HeaderCell>
          <Table.HeaderCell>Actions</Table.HeaderCell>
        </Table.Row>
      </Table.Header>
      <Table.Body>
        {solutions.map((item) => (
          <SolutionItem key={uuid()} item={item} />
        ))}
      </Table.Body>
    </Table>
  );
}

const Solutions = () => {
  const { solutions, solutions_error, solutions_loading } = useSelector(state => state.solutions)
  const dispatch = useDispatch()
  useEffect(() => {
    solutions.length === 0 && dispatch(getSolutions())
  }, [])
  return (
    <PageContainer className='solutions-container' title='Solutions'>
      <Header as='h2'>
        My Solutions
          <Header.Subheader>You can find your solutions below</Header.Subheader>
      </Header>
      {renderSolutions(solutions, solutions_error, solutions_loading)}
    </PageContainer>
  );
}

export default Solutions;
