import Types from 'constants/types';

const INITIAL_STATE = {
  login_loading: false,
  login_error: '',
  register_loading: false,
  register_error: '',
  userDetails: null,
  userDetailsLoading: false,
  userDetailsError: ''
};

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case Types.AUTH_LOGIN_SUCCESS:
      return {
        ...state,
        login_error: '',
        login_loading: false
      };
    case Types.AUTH_LOGIN_LOADING:
      return {
        ...state,
        login_loading: action.payload
      };
    case Types.AUTH_LOGIN_ERROR:
      return {
        ...state,
        login_error: action.payload
      };
    case Types.AUTH_REGISTER_SUCCESS:
      return {
        ...state,
        register_error: '',
        register_loading: false
      };
    case Types.AUTH_REGISTER_LOADING:
      return {
        ...state,
        register_loading: action.payload
      };
    case Types.AUTH_REGISTER_ERROR:
      return {
        ...state,
        register_error: action.payload
      };
    case Types.GET_USER_DETAILS_SUCCESS:
      return {
        ...state,
        userDetails: action.payload
      };
    case Types.GET_USER_DETAILS_LOADING:
      return {
        ...state,
        userDetailsLoading: action.payload
      };
    case Types.GET_USER_DETAILS_ERROR:
      return {
        ...state,
        userDetailsError: action.payload
      };
    default:
      return state;
  }
};
