import Types from 'constants/types';
import api from 'constants/api';
import { errorsHandler, loading, dataPayload, error } from 'utilities';

/**
 * Component - Login
 * Log in user
 *
 *
 * @param {string} email
 * @param {string} password
 * @returns {object} - email, token
 */
export const loginUser = (email, password) => (dispatch) =>
  new Promise(async (resolve, reject) => {
    dispatch(loading(Types.AUTH_LOGIN_LOADING, true));
    dispatch(error(Types.AUTH_LOGIN_ERROR, ''));
    try {
      let result = await api.loginUserApi(email, password);
      const { status, data } = result;
      if (status === 200) {
        await saveLogInDetails(data.token).then(() => resolve());
      } else {
        dispatch(error(Types.AUTH_LOGIN_ERROR, 'Something went wrong!'));
      }
      dispatch(loading(Types.AUTH_LOGIN_LOADING, false));
    } catch (ex) {
      dispatch(loading(Types.AUTH_LOGIN_LOADING, false));
      dispatch(error(Types.AUTH_LOGIN_ERROR, errorsHandler(ex)));
    }
  });

/**
 * Component - Register
 * Register an user
 *
 * @param {string} name
 * @param {string} lastname
 * @param {string} email
 * @param {string} password
 * @param {string} confirmPassword
 * @returns {object} - _id, name, lastname, email
 */
export const registerUser = (
  name,
  lastname,
  email,
  password,
  confirmPassword
) => (dispatch) =>
    new Promise(async (resolve, reject) => {
      dispatch(loading(Types.AUTH_REGISTER_LOADING, true));
      dispatch(error(Types.AUTH_REGISTER_ERROR, ''));
      try {
        let result = await api.registerUserApi(
          name,
          lastname,
          email,
          password,
          confirmPassword
        );
        console.log(result);
        const { status, headers } = result;
        if (status === 200) {
          await saveLogInDetails(headers['x-auth-token']).then(() => resolve());
        } else {
          dispatch(error(Types.AUTH_REGISTER_ERROR, 'Something went wrong!'));
        }
        dispatch(loading(Types.AUTH_REGISTER_LOADING, false));
      } catch (ex) {
        dispatch(loading(Types.AUTH_REGISTER_LOADING, false));
        dispatch(error(Types.AUTH_REGISTER_ERROR, errorsHandler(ex)));
      }
    });

/**
 * Component - Profile
 * Get User Details
 *
 * @returns {object} - status, data [name, lastname, email, created_at, updatedAt]
 */
export const getUserDetails = () => async (dispatch) => {
  dispatch(loading(Types.GET_USER_DETAILS_LOADING, true));
  dispatch(error(Types.GET_USER_DETAILS_ERROR, ''));
  try {
    let result = await api.userDetailsApi();
    const { status, data } = result;
    if (status === 200) {
      dispatch(dataPayload(Types.GET_USER_DETAILS_SUCCESS, data.data[0]))
    } else {
      dispatch(error(Types.GET_USER_DETAILS_ERROR, 'Something went wrong!'));
    }
    dispatch(loading(Types.GET_USER_DETAILS_LOADING, false));
  } catch (ex) {
    dispatch(loading(Types.GET_USER_DETAILS_LOADING, false));
    dispatch(error(Types.GET_USER_DETAILS_ERROR, errorsHandler(ex)));
  }
};

/**
 * Save user details
 *
 * @param {*} token
 * @param {*} countOfCategories
 * @param {*} google
 * @param {*} facebook
 */
const saveLogInDetails = async (token) => {
  await localStorage.setItem(process.env.REACT_APP_ACCESS_TOKEN, token);
};
