import React from 'react'
import Types from 'constants/types';
import api from 'constants/api';
import { toast } from 'react-toastify';
import { errorsHandler, loading, dataPayload, error } from 'utilities';
import { Icon } from 'semantic-ui-react';

/**
 * Component - Solutions
 * Get Solutions
 *
 * @returns {array} - solution
 */
export const getSolutions = () => async (dispatch) => {
  dispatch(loading(Types.GET_SOLUTIONS_LOADING, true));
  dispatch(error(Types.GET_SOLUTIONS_ERROR, ''));
  try {
    let result = await api.getSolutionsApi();
    const { status, data } = result;
    if (status === 200) {
      dispatch(dataPayload(Types.GET_SOLUTIONS_SUCCESS, data))
    } else {
      dispatch(error(Types.GET_SOLUTIONS_ERROR, 'Something went wrong!'));
    }
    dispatch(loading(Types.GET_SOLUTIONS_LOADING, false));
  } catch (ex) {
    dispatch(loading(Types.GET_SOLUTIONS_LOADING, false));
    dispatch(error(Types.GET_SOLUTIONS_ERROR, errorsHandler(ex)));
  }
};

