/**
 * Component - Solutions
 * Validate Solution
 *
 * @returns {object} - solution
 */
export const validateSolution = (file, fileDetails, instance) => (dispatch) => new Promise(async (resolve, reject) => {
  dispatch(loading(Types.VALIDATE_SOLUTION_LOADING, true));
  dispatch(error(Types.VALIDATE_SOLUTION_ERROR, ''));
  try {
    let result = await api.validateSolutionApi(file, fileDetails, instance);
    console.log('result', result)
    const { status, data } = result;
    if (status === 200) {
      toast.success(
        <>
          <Icon name="thumbs up" /> Solution is validated successfully!
        </>
      );
      dispatch(dataPayload(Types.VALIDATE_SOLUTION_SUCCESS, data))
    } else {
      dispatch(error(Types.VALIDATE_SOLUTION_ERROR, 'Something went wrong!'));
    }
    dispatch(loading(Types.VALIDATE_SOLUTION_LOADING, false));
  } catch (ex) {
    dispatch(loading(Types.VALIDATE_SOLUTION_LOADING, false));
    dispatch(error(Types.VALIDATE_SOLUTION_ERROR, errorsHandler(ex)));
  }
})

export const resetValidation = () => {
  return {
    type: Types.RESET_VALIDATE_STATE
  }
}