import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { PageContainer } from 'components/shared';
import { Header, Table, Message, Loader } from 'semantic-ui-react';
import { v4 as uuid } from 'uuid';
import { getAllSolutions } from 'actions';
import AllSolutionsItem from './AllSolutionsItem';
import './allSolutions.scss';

const renderSolutions = (solutions, solutions_error, solutions_loading) => {
  if (solutions_loading) {
    return <Loader active inline='centered' />;
  }
  if (solutions_error) {
    return (
      <Message
        warning
        header={solutions_error}
        content='Visit our registration page, then try again.'
      />
    );
  }
  if (solutions.length === 0) {
    return <Message content='No solutions were found.' />;
  }
  return (
    <Table color='teal'>
      <Table.Header>
        <Table.Row>
          <Table.HeaderCell>#</Table.HeaderCell>
          <Table.HeaderCell>Team name</Table.HeaderCell>
          <Table.HeaderCell>a_example</Table.HeaderCell>
          <Table.HeaderCell>b_lovely_landscapes</Table.HeaderCell>
          <Table.HeaderCell>c_memorable_moments</Table.HeaderCell>
          <Table.HeaderCell>d_pet_pictures</Table.HeaderCell>
          <Table.HeaderCell>e_shiny_selfies</Table.HeaderCell>
          <Table.HeaderCell>Total score</Table.HeaderCell>
        </Table.Row>
      </Table.Header>
      <Table.Body>
        {solutions.map((item, index) => (
          <AllSolutionsItem key={uuid()} item={item} index={index} />
        ))}
      </Table.Body>
    </Table>
  );
};

const Solutions = () => {
  const {
    allSolutions,
    allSolutions_error,
    allSolutions_loading
  } = useSelector((state) => state.solutions);
  const dispatch = useDispatch();
  useEffect(() => {
    allSolutions.length === 0 && dispatch(getAllSolutions());
  }, []);
  return (
    <PageContainer className='allSolutions-container' title='All Solutions'>
      <Header as='h2'>
        Leaderboard
        <Header.Subheader>
          You can see all solutions sorted by score here
        </Header.Subheader>
      </Header>
      {renderSolutions(allSolutions, allSolutions_error, allSolutions_loading)}
     </PageContainer>
  );
};

export default Solutions;
