import Types from 'constants/types';

const INITIAL_STATE = {
  solutions: [],
  solutions_error: '',
  solutions_loading: false,
  allSolutions: [],
  allSolutions_error: '',
  allSolutions_loading: false,
  validateSolutionLoading: false,
  validateSolutionError: '',
  validateSolutionSuccess: false,
  validationScore: null
};

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case Types.GET_SOLUTIONS_SUCCESS:
      return {
        ...state,
        solutions: action.payload,
        solutions_error: '',
        solutions_loading: false
      };
    case Types.GET_SOLUTIONS_LOADING:
      return {
        ...state,
        solutions_loading: action.payload
      };
    case Types.GET_SOLUTIONS_ERROR:
      return {
        ...state,
        solutions_error: action.payload
      };
    case Types.GET_ALL_SOLUTIONS_SUCCESS:
      return {
        ...state,
        allSolutions: action.payload,
        allSolutions_error: '',
        allSolutions_loading: false
      };
    case Types.GET_ALL_SOLUTIONS_LOADING:
      return {
        ...state,
        allSolutions_loading: action.payload
      };
    case Types.GET_ALL_SOLUTIONS_ERROR:
      return {
        ...state,
        allSolutions_error: action.payload
      };
    case Types.VALIDATE_SOLUTION_SUCCESS:
      return {
        ...state,
        solutions: [],
        allSolutions: [],
        validateSolutionSuccess: true,
        validationScore: action.payload.score
      };
    case Types.VALIDATE_SOLUTION_LOADING:
      return {
        ...state,
        validateSolutionLoading: action.payload
      }
    case Types.VALIDATE_SOLUTION_ERROR:
      return {
        ...state,
        validateSolutionError: action.payload
      }
    case Types.RESET_VALIDATE_STATE:
      return {
        ...state,
        validateSolutionLoading: false,
        validateSolutionError: '',
        validateSolutionSuccess: false
      }
    default:
      return state;
  }
};
