import React from 'react';
import { Redirect } from 'react-router-dom';

/**
 * Check if user is authenticated so to have permission to enter specified route, if not redirect them to Login Page
 *
 * @param {*} Component
 */
export function protectedComponent(Component) {
  return class ProtectedComponent extends React.Component {
    isAuthenticated() {
      let token = localStorage.getItem(process.env.REACT_APP_ACCESS_TOKEN);
      if (token === null) {
        return false;
      }
      return true;
    }
    render() {
      if (this.isAuthenticated()) {
        return <Component {...this.props} />;
      } else return <Redirect to='/login' />;
    }
  };
}
export default protectedComponent;
