import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { PageContainer } from 'components/shared';
import { Header, Table, Message, Loader } from 'semantic-ui-react';
import { v4 as uuid } from 'uuid';
import { getInstances } from 'actions'
import InstanceItem from './InstanceItem';
import './instances.scss';

const renderInstances = (instances, instances_error, instances_loading) => {
  if (instances_loading) {
    return <Loader active inline='centered' />
  }
  if (instances_error) {
    return <Message
      warning
      header={instances_error}
      content='Visit our registration page, then try again.'
    />
  }
  if (instances.length === 0) {
    return <Message
      content='No instances were found.'
    />
  }
  return (
    <Table color='teal'>
      <Table.Header>
        <Table.Row>
          <Table.HeaderCell>Name</Table.HeaderCell>
          <Table.HeaderCell>Description</Table.HeaderCell>
          <Table.HeaderCell>Post date</Table.HeaderCell>
          <Table.HeaderCell>Actions</Table.HeaderCell>
        </Table.Row>
      </Table.Header>
      <Table.Body>
        {instances.map((item) => (
          <InstanceItem key={uuid()} item={item} />
        ))}
      </Table.Body>
    </Table>
  )
}

const Instances = () => {
  const { instances, instances_error, instances_loading } = useSelector(state => state.instances)
  const dispatch = useDispatch()
  useEffect(() => {
    instances.length === 0 && dispatch(getInstances())
  }, [])
  return (
    <PageContainer className='instances-container' title='Instances'>
      <Header as='h2'>
        Instances
          <Header.Subheader>You can download instances below</Header.Subheader>
      </Header>
      {renderInstances(instances, instances_error, instances_loading)}
    </PageContainer>
  );
}

export default Instances;
