import Types from 'constants/types';

const INITIAL_STATE = {
  instances: [],
  instances_error: '',
  instances_loading: false
};

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case Types.GET_INSTANCES_SUCCESS:
      return {
        ...state,
        instances: action.payload,
        instances_error: '',
        instances_loading: false
      };
    case Types.GET_INSTANCES_LOADING:
      return {
        ...state,
        instances_loading: action.payload
      };
    case Types.GET_INSTANCES_ERROR:
      return {
        ...state,
        instances_error: action.payload
      };
    default:
      return state;
  }
};
