import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { PageContainer } from 'components/shared';
import { toast } from 'react-toastify';
import {
  Header,
  Segment,
  Icon,
  Button,
  Input,
  Message,
  Select
} from 'semantic-ui-react';
import { validateSolution, resetValidation } from 'actions';
import './validation.scss';

const SUPPORTED_FORMATS = ['text/plain'];

const Validation = () => {
  const [file, setFile] = useState(null);
  const [instance, setInstance] = useState(null);
  const [fileError, setFileError] = useState('');
  const {
    validationScore,
    validateSolutionLoading,
    validateSolutionError,
    validateSolutionSuccess
  } = useSelector((state) => state.solutions);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(resetValidation());
  }, []);

  const instancesOptions = [
    { key: 'a_example', value: 'a_example', text: 'a_example' },
    {
      key: 'b_lovely_landscapes',
      value: 'b_lovely_landscapes',
      text: 'b_lovely_landscapes'
    },
    {
      key: 'c_memorable_moments',
      value: 'c_memorable_moments',
      text: 'c_memorable_moments'
    },
    { key: 'd_pet_pictures', value: 'd_pet_pictures', text: 'd_pet_pictures' },
    {
      key: 'e_shiny_selfies',
      value: 'e_shiny_selfies',
      text: 'e_shiny_selfies'
    }
  ];

  /**
   * Handler for file upload
   * @param {*} e
   */
  const getFile = (e) => {
    setFileError('');
    let file = e.target.files[0];
    if (file) {
      // Make new FileReader
      let reader = new FileReader();

      // Convert the file to base64 text
      reader.readAsDataURL(file);

      // on reader load
      reader.onload = () => {
        // Make a fileInfo Object
        let base64Unformatted = reader.result;
        let fileBase64 = base64Unformatted.split(',')[1];
        if (!SUPPORTED_FORMATS.includes(file.type)) {
          setFileError('File format is not supported. Only text file allowed!');
          setFile(null);
          return;
        }
        console.log('fileBase64', fileBase64, 'file name', file.name);
        setFile({
          file: fileBase64,
          fileDetails: { name: file.name, mimetype: file.type }
        });
      };
    }
  };

  /**
   * Handler on button click
   */
  const validateSolutionHandler = () => {
    if (!instance) {
      return toast.warn(
        <>
          <Icon name='info' /> You must select an instance!
        </>
      );
    }
    if (file) {
      return dispatch(validateSolution(file.file, file.fileDetails, instance));
    }
  };

  /**
   * Show errors of validation
   *
   * @param {string} errors
   */
  const showErrors = (errors) => {
    let errorsArray = errors.split(',');
    return errorsArray.map((error) => (
      <>
        <span>{error}</span>
        <br />
      </>
    ));
  };

  return (
    <PageContainer className='validation-container' title='Validation'>
      <Header as='h2'>
        Validate solution
        <Header.Subheader>
          Upload the solution and check if is correct
        </Header.Subheader>
      </Header>
      <Segment placeholder>
        <Header icon>
          <Icon name='upload' />
          Upload the solution
        </Header>
        <Select
          placeholder='Select instance'
          options={instancesOptions}
          onChange={(e) => setInstance(e.target.textContent)}
          disabled={validateSolutionSuccess}
        />
        <Input
          type='file'
          onChange={getFile}
          disabled={validateSolutionSuccess}
        />
        <Segment.Inline>
          <Button
            primary
            disabled={file === null || validateSolutionSuccess}
            onClick={validateSolutionHandler}
            loading={validateSolutionLoading}
          >
            Validate
          </Button>
        </Segment.Inline>
      </Segment>
      {fileError && (
        <Message warning header={'File upload error'} content={fileError} />
      )}
      {validateSolutionError && (
        <Message
          warning
          header={'Violations'}
          content={showErrors(validateSolutionError)}
        />
      )}
      {validateSolutionSuccess && (
        <Message
          success
          header={'Evaluation'}
          content={`Total score: ${validationScore}`}
        />
      )}
    </PageContainer>
  );
};

export default Validation;
